.phase {
  text-align: center;
}

.board {
  min-height: 300px;
}

.game-area {
  display: flex;
  flex-wrap: wrap;
  padding: 10px;

  .card {
    display: flex;
    flex-direction: row;
    margin: 10px 2px;
  }
}

.players {
  display: flex;
  min-height: 350px;

  .player {
    width: 25%;

    &.active, &.pullActive {
      .player-hand {
        filter: blur(0);
      }
      border: 3px solid red;
    }

    //&.pullActive {
    //  border: 3px solid purple;
    //}
  }

  .player-hand {
    display: flex;
    flex-wrap: wrap;
    filter: blur(40px);
  }
}

.card {
  margin: 10px;
  border: 1px solid black;

  .side {
    width: 50px;
    height: 50px;
  }
}


.deck {
  display: flex;
  flex-wrap: wrap;
}
